@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;900&display=swap');

*{
    font-family: 'Robot', sans-serif;
    font-size: 30px;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    /* width: 56%; */
    text-align: center;
    border: 10px solid #101116;
    background-color: #101116;
    border-radius: 5px;
}

.keyboard{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: minmax(60px, auto);
}

input[type="text"]{
    height: 75px;
    width: 480px;
    background-color: #10111600;
    border: 0px;
    color: #ffffff;
    text-align: right;
    font-size: 25px;
    font-weight: 300px;
    line-height: 1px;
}

#clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;
    color: red;
}
.name{
    color: red;
    padding: 5rem;
}


#backspace{
    color: #262834;
}
button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #262834;
    color: #fff;
    font-weight: 500;
}
input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}
.highlight{
    background-color: lightblue;
}